import { PageHeader } from "@/components/page-header";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { pricingPlans } from "@/lib/data";
import { cn } from "@/lib/utils";
import { CheckCircle2, Gem, Star } from "lucide-react";

export default function PricingPage() {
  return (
    <div className="flex-1 space-y-4 pt-6">
      <PageHeader
        title="Subscription Plans"
        description="Choose a plan that fits your needs and unlock more benefits."
      />
      <div className="grid grid-cols-1 lg:grid-cols-2 xl:grid-cols-4 gap-8 max-w-7xl mx-auto pt-8">
        {pricingPlans.map((plan) => (
          <Card key={plan.id} className={cn("flex flex-col", (plan.id === 'premium' || plan.id === 'donator') && "border-primary shadow-lg")}>
            <CardHeader>
              <div className="flex items-center justify-between">
                <CardTitle className="font-headline">{plan.name}</CardTitle>
                {plan.id === 'donator' && <Gem className="text-blue-500" />}
                {plan.id === 'premium' && <Star className="text-yellow-500" />}
                {plan.id === 'enterprise' && <CheckCircle2 className="text-green-500" />}
              </div>
              <CardDescription>{plan.description}</CardDescription>
            </CardHeader>
            <CardContent className="flex-grow">
              <div className="mb-6">
                {typeof plan.price === 'number' ? (
                  <>
                    <span className="text-4xl font-bold font-headline">${plan.price}</span>
                    <span className="text-muted-foreground">{plan.priceDescription}</span>
                  </>
                ) : (
                  <span className="text-3xl font-bold font-headline">{plan.price}</span>
                )}
              </div>
              <ul className="space-y-3">
                {plan.features.map((feature, index) => (
                  <li key={index} className="flex items-start">
                    <CheckCircle2 className="h-5 w-5 text-green-500 mr-2 mt-0.5 shrink-0" />
                    <span>{feature}</span>
                  </li>
                ))}
              </ul>
            </CardContent>
            <CardFooter>
              <Button className={cn("w-full", plan.id !== 'premium' && plan.id !== 'donator' && "variant-outline")}>
                {plan.id === 'basic' ? "Subscribe" : plan.id === 'enterprise' ? 'Contact Us' : 'Get Started'}
              </Button>
            </CardFooter>
          </Card>
        ))}
      </div>
    </div>
  )
}
